@import url('https://fonts.googleapis.com/css2?family=Jua&display=swap');
html{
  height: 100%;
  width: 100%;
}
body{
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  font-weight: 300;
}
.row{
  display: flex;
  flex-wrap: wrap;
}
#root{
  width: 100%;
  height: 100%;
}
.App{ 
  font-family: 'Jua', sans-serif;
  box-sizing: border-box;
  background-color:white;
  color:  #282c34;
  width: 100%;
  height:100%;
}
.sort-l{
  display: flex;
  align-items: center;
  justify-content: left;
}
.navsort{
  display: flex;
  align-items: center;
}
.sort{
  display: flex;
  align-items: center;
  justify-content: center;
}
.sort-r{
  display: flex;
  align-items: center;
  justify-content: right;
}
.Nav{
  position: relative;
  min-height:65px;
  height: auto;
  padding: 2.25%;
  background-color: #282c34 ;
  color:white;
  font-size: 3vh;
  p{
    text-align: right;
    font-family: 'Jua', sans-serif;
    margin: 0px;
  }
  .list{
    height: 5.5vh;
    width:5.5vh;
    position: absolute;
    right: 5%;
    color: #282c34;
    border-radius: 15%;
    border: 3px solid white;
    .sort{
      display: flex;
      align-items: center;
      justify-content: center;
    }
  }
}

.sidebar1{
  opacity: 1;
  transition: all 1s;
  position: fixed;
  top:15%;
  left:5%;
  background-color: white;
  width: 90%;
  height: 50%;
  z-index: 10;
  border-radius: 10px;
  border: 3px solid black;
  .sidecont{
    box-sizing: border-box;
    padding: 2.5%;
    padding-top: 0%;
    padding-bottom: 0%;
    height: 15%;
    width: 100%;
    div{
      font-size: 2.5vh;
      border-bottom: 1px solid black;
      p{
        font-weight: 300;
        margin: 0px;
      }
    }
  }
}
.sidebar2{
  display: none;
  opacity: 0;
  transition: all 1s;
  z-index: 10;
  position: fixed;
  top:15%;
  left:5%;
  height: 50%;
  width: 90%;
  background-color: white;
  color: white;
}

.input{
  border-top: 5px  solid #282c34;
  border-bottom: 5px  solid #282c34;
  box-sizing: border-box;
  padding: 2.5%;
  width: 100%;
  height: 60%;
  background-color: rgb(255, 255, 255);
  .in{
    padding: 1%;
    width: 100%;
    height: 15%;
    margin-bottom: 2.5%;
    border-bottom:  1px solid black;
  }
  .inbtn{
    transition: all 0.5s;
    font-family: 'Jua', sans-serif;
    font-weight: 230;
    width: 25%;
    height: 90%;
    background-color: white;
    color: #282c34;
    font-size: 2.5vh;
    border:4px solid #282c34;
    border-radius: 10px;
    margin-right: 10px;
  }
  .card{
    padding: 1%;
    width: 100%;
    height: 30%;
    border-bottom:  1px solid black;
  }
  .cardbtn{
    transition: all 0.5s;
    font-family: 'Jua', sans-serif;
    width: 25%;
    height: 40%;
    background-color: white;
    color: #282c34;
    font-size: 2.5vh;
    border:4px solid #282c34;
    border-radius: 10px;
    margin-right: 10px;
    margin-bottom: 5%;
  }
  .on{
    width: 26%;
    transition: all 0.5s;
    background-color: #282c34;
    color: white;
  }
  .inputbox{
    padding: 1%;
    width: 100%;
    height: 30%;
    div{
      height:33%;
      vertical-align: baseline;
      p{
        font-size: 3vh;
        margin-right:5%;
        margin-bottom: 3%;
      }
      .day{
        font-size: 2.5vh;
        width: 15%;
        background-color: rgb(255, 255, 255);
        border: 0px;
        border-bottom: 5px solid #282c34;
        margin-right: 3.5%;
        text-align: center;
      }
      .memo{
        font-size: 2.5vh;
        width: 55%;
        background-color: rgb(255, 255, 255);
        border: 0px;
        border-bottom: 5px solid #282c34;
        margin-right: 2.5%;
        text-align: center;
      }
    }
    .inputbtn{
      margin-top: 5%;
      padding: 2.5%;
      height: 33%;
      .btn{
        transition: all 0.25s;
        width: 80%;
        height: 100%;
        background-color: #282c34;
        color: white;
        border-radius: 15px;
        font-size: 3vh;
        
      }
      .on{
        transition:all 0.25s;
        border: 5px solid #282c34;
        background-color: white;
        color: #282c34;
      }
    }
  }
}
.last{
  margin-bottom: 20px;
  width: 100%;
  height: 20%;
  background-color: rgb(255, 255, 255);
  border-top: 5px solid #282c34;
  border-bottom: 5px solid #282c34;
  .content{
    width: 90%;
    height: 80%;
    .conin{
      margin-bottom: 10px;
      border-bottom: 3px solid #282c34;
    }
    p{
      font-size: 2.75vh;
      margin:0px 8px;
    }
  }

}
.cardselect{
  margin-top: 2.5%;
  box-sizing: border-box;
  width: 100%;
  height: auto;
  border-top: 5px solid #282c34;
  border-bottom: 5px solid #282c34;
  background-color: rgb(255, 255, 255);
  .selectbox{
    box-sizing: border-box;
    padding: 2.5%;
    width: 100%;
    height: 50%;
    .btn{
      font-family: 'Jua', sans-serif;
      border: 3px solid #282c34;
      margin-right: 5%;
      width: 25%;
      background-color: #eee;
      border-bottom: 5px solid #282c34;
      font-size: 2vh;
      font-weight: 800;
      border-radius: 10px;
    }
    .on{
      border-bottom: 2.5px solid #282c34;
    }
  }
}

.selectdata{
  padding: 2.5%;
  margin-top: 3vh;
  border-top: 5px solid #282c34;
  border-bottom: 5px solid #282c34;
  .selectm{
    border-bottom: 3px solid #282c34;
  }
  .selectdiv{
    font-size: 2.5vh;
  }
  .col-9{
    width: 70%;
  }
  .col-3{
    width: 30%;
    button{
      font-family: 'Jua', sans-serif;
      padding: 5%;
      height: 5.5vh;
      font-size: 2.75vh;
      border: 3px solid #282c34;
      border-radius: 10px;
      background-color: #282c34;
      color: white;
    }
  }
}

.cashmain{
  padding: 2.5%;
  margin-top: 3vh;
  border-top: 5px solid #282c34;
  border-bottom: 5px solid #282c34;
  .title{
    border-top: 3px solid #282c34;
    border-bottom: 3px solid #282c34;
    margin-bottom: 15px;
  }
}

.saving{
  padding: 2.5%;
  width: 100%;
  height: 60%;
  .savingbtn{
    height: 20%;
  }
}
.savinginput{
  height: 60%;
  padding-left: 10%;
  p{
    font-size: 3vh;
    margin: 0%;
    margin-right: 2.5%;
  }
  .memo{
    font-size: 2.5vh;
    width: 55%;
    background-color: rgb(255, 255, 255);
    border: 0px;
    border-bottom: 5px solid #282c34;
    margin-right: 2.5%;
    text-align: center;
  }
  .day{
    font-size: 2.5vh;
    width: 15%;
    background-color: rgb(255, 255, 255);
    border: 0px;
    border-bottom: 5px solid #282c34;
    margin-right: 3.5%;
    text-align: center;
  }
  div{
    height: 30%;
  }
}

.showmain{
  border-top: 5px solid #282c34;
  border-bottom: 5px solid #282c34;
  padding: 2.5%;
  margin-top: 3vh;
  .line{
    border-bottom: 3px solid #282c34;
  }
  p{
    font-size: 2.8vh;
    margin: 0px;
    margin-bottom: 0.5rem;
  }
}